header {
  box-shadow: 0px 2px 4px var(--link-second-color);
  background: var(--backgroound-section-color);
  background-position: 25% 90%;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
  padding: 12px 15px;
  display: flex;
}

.nav-mobile {
  border: none;
  padding: 8px;
  display: block;
}

.header_title {
  color: #0000;
  background-image: linear-gradient(#0078ff80, #0009), url("background-header.ac9671e9.png");
  background-position-y: bottom;
  background-size: 100%;
  -webkit-background-clip: text;
  background-clip: text;
  align-items: center;
  padding: .5em;
  display: flex;
}

.header_img {
  border-radius: 50%;
  height: 32px;
}

.header_title-text {
  font-size: calc(9vw - 13.68px);
  display: none;
}

.nav {
  color: var(--background-secondary-color);
  display: none;
}

.tablet-nav_menu {
  display: flex;
}

.nav-item {
  padding: 4px 6px 6px;
  font-size: 1rem;
  font-weight: 600;
}

.auth-link_contanier {
  visibility: collapse;
  align-items: center;
  padding: 8px;
  display: flex;
}

.auth-link_text {
  margin-left: 8px;
  font-size: 1rem;
  display: none;
}

.activ {
  color: #f1f2f3;
  border: 2px solid #606c78;
  border-radius: 8px;
}

.mobile-nav {
  z-index: 1;
  background-color: #606c7880;
  width: 100vw;
  height: 100vh;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.mobile-nav_menu {
  box-sizing: border-box;
  background-color: var(--background-secondary-color);
  z-index: 2;
  width: 40vw;
  min-width: 280px;
  height: 100%;
  padding: 60px 12px 12px;
  position: relative;
  box-shadow: 4px 0 8px #606c78;
}

.mobile-nav_link {
  color: #d2d7e3;
}

.activ-link {
  color: #fff;
}

.mobile-nav_button-close {
  z-index: 2;
  position: absolute;
  top: 8px;
  right: 8px;
}

@media (width >= 380px) {
  .header_title-text {
    margin-left: 6px;
    display: inline;
  }
}

@media (width >= 487px) {
  .header_title-text {
    font-size: 2rem;
  }

  .header_img {
    height: 36px;
  }
}

@media (width >= 767px) {
  .mobile-nav, .nav-mobile {
    display: none;
  }

  .nav {
    display: block;
  }
}

@media (width >= 791px) {
  .auth-link_text {
    display: inline;
  }
}

.qwe {
  fill: red;
  border: 1px solid green;
  width: 20px;
  height: 50px;
}
